.partners-wrapper {
  padding: px-to-rem(113px) px-to-rem(30px) px-to-rem(120px);
  @media(max-width: 767px) {
    padding-bottom: 0; }


  .tech__items {
    flex-wrap: wrap;
    justify-content: center;
    margin-top: px-to-rem(40px); }

  .tech-item {
    width: 33.33%;
    @media(max-width: 1023px) {
      width: 50%; }
    @media(max-width: 767px) {
      width: 100%; }


    &__header {
      @media(max-width: 767px) {
        margin-bottom: px-to-rem(35px); } }


    &__content {
      letter-spacing: normal;
      text-decoration: none; }

    &__read-more {
      display: inline-flex; } } }



