        .custom_input {
          border-radius: 0;
          top: 4px;

          &.alert {
            border: 2px solid red; }

          &:after {
            border-radius: 0;
            background: url('/static/img/pictures/pre-sale/check.svg') no-repeat;
            width: 18px;
            height: 14px; } }

        input {
          margin-left: -60px;
          margin-right: 46px !important; } }

      .option {
        // color: #000000
        cursor: pointer;
        padding-left: 15px;
        color: #c7cbfd;
        line-height: 24px;
        font-size: 13px;
        @media(max-width:1023px) {
          line-height: 30px; } }

      select {
        font-size: 13px;

        &:focus {
            outline: none; } } }

    .confirm_form {
      padding-top: 114px;
      position: absolute;
      font-weight: 500;
      color: #32ba94;
      font-size: 24px;
      text-align: center;
      width: 100%;

      @media(max-width:767px) {
        padding-top: 40px; } }

    .confirm_form_bg {
      position: absolute;
      top: -10px;
      left: -10px;
      right: -10px;
      bottom: -10px;
      background-color: rgba(0, 0, 0, 0);
      border-radius: 10px; }

    .opacity {

      .form_grp,
      .sm-form-btn {
        opacity: .3; }

      .sm-form-btn:hover {
        opacity: .3;

        svg {
          transform: none; }

        .sm-form-btn__text {
          transform: none; } } } }

  .patterned {

    .block1,
    .block2 {
      position: relative;
      z-index: -5;
      @media(max-width:1023px) {
        display: none; } }

    .block1 {
      top: 600px; }

    .block2 {
      top: 2850px; }

    img {
      position: absolute;
      width: 30px;
      height: 30px; }

    .triangle_1 {
      left: 150px;
      top: 120px; }

    .triangle_2 {
      top: 180px;
      transform: rotate(90deg);
      left: 190px; }

    .triangle_5 {
      top: 200px; }

    .triangle_3 {
      top: 400px;
      width: 130px;
      height: 130px;
      left: 15%; }

    .triangle_4 {
      top: 0;
      right: 14%;
      width: 100px;
      height: 80px;
 }      // transform: scaleY(-1)

    .triangle_7 {
      top: 100px;
      right: 12%; }

    .triangle_8 {
      top: 360px;
      width: 30px;
      left: 12%; }

    .triangle_6,
    .triangle_66 {
      width: 1200px;
      height: 400px;
      right: -30%;
      top: 950px;
      @media(max-width:1440px) {
        right: -80%;
        display: none; } }

    .triangle_66 {
      top: 4000px;
      right: auto;
      left: -30%; }

    .triangle_21 {
      width: 30px;
      right: 20%;
      top: 2000px; }


    .triangle_22 {
      width: 130px;
      height: 100px;
      top: 2100px;
      right: 13%; } } }



