html {
  background: $dark; }


.app {
  &.index {
    background: url('/static/img/patterns/diagonals.svg') no-repeat center 760px;
    background-size: 2560px auto;
    height: auto; }

  &.roadmap {
    background: url('/static/img/patterns/roadmap-pattern.png') no-repeat center 0;
    background-size: 100% auto;
    height: auto;

    .page-content {
      padding-bottom: px-to-rem(200px);
      @media(max-width: 767px) {
        padding-bottom: px-to-rem(60px); } } }


  &.faq {
    .page-content-pattern {
      &::after {
        bottom: inherit;
        top: 115px; } } } }


.patterns {
  .pattern {
    pointer-events: none;
    position: absolute;
    user-select: none; } }


.dot-pattern-wrapper {
  background: url('/static/img/patterns/dots.svg') bottom; }


.team-wrapper {
  background: url('/static/img/patterns/radial-gradient.svg') no-repeat center;
  background-size: auto 100%;
  position: relative;

  &::after {
    bottom: -20rem;
    background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,$dark 50%,rgba(0,0,0,0) 100%);
    content: '';
    height: 40rem;
    left: 0;
    position: absolute;
    right: 0; } }

.page-content-pattern {
  position: relative;

  &::before,
  &::after {
    background: url('/static/img/patterns/page-pattern-radical-gradient.svg') no-repeat center center;
    background-size: cover;
    content: '';
    width: px-to-rem(720px);
    height: px-to-rem(720px);
    position: absolute;
    z-index: 3; }

  &::before {
    top: -130px;
    left: -200px; }

  &::after {
    bottom: -190px;
    right: -230px; } }

.page-content {
  position: relative;
  z-index: 4; }


// Layer up the team-wrapper shadow
.partners-wrapper,
.join-us-wrapper,
.press-wrapper {
  position: relative;
  z-index: 2; }


.join-us-wrapper {
  background: url('/static/img/patterns/footer-ornament.svg') center center repeat-x $dark;
  background-size: auto 33rem;
  @media(max-width: 1023px) {
    background: transparent; } }


