.components-wrapper {
  padding: px-to-rem(113px) px-to-rem(30px) px-to-rem(156px);
  @media(max-width: 767px) {
    padding-bottom: 0; } }


.components {
  margin: 0 auto;
  max-width: 1160px;

  &__inner {
    @media(max-width: 767px) {
      display: flex;
      flex-direction: column-reverse; } }


  &__items {
    padding-top: px-to-rem(98px);
    @media(max-width: 767px) {
      padding-top: 40px; } }


  &__nav {
    margin-top: px-to-rem(103px);
    @media(max-width: 767px) {
      margin: 35px 0 -5px; } } }


.component {
  display: flex;
  @media(max-width: 767px) {
    flex-direction: column; }


  &:not(.active) {
    display: none; }


  &__content {
    color: #6e7195;
    letter-spacing: px-to-rem(.5px);
    line-height: 1.28;
    margin-right: px-to-rem(55px);
    margin-top: px-to-rem(-6px);
    width: px-to-rem(430px);
    @media(max-width: 1023px) {
      flex-shrink: 0;
      width: 50%; }
    @media(max-width: 767px) {
      width: 100%; }


    h4 {
      font-size: px-to-rem(24px);
      font-weight: normal;
      margin: 0; }

    h4,
    p,
    ul {
      margin: px-to-rem(20px) 0;

      &:first-child {
        margin-top: 0; }

      &:last-child {
        margin-bottom: 0; } }

    .text-lighter {
      color: #9fa2d0; } }

  &__image {
    @media(max-width: 767px) {
      margin-top: 40px; }


    img {
      border-radius: 15px;
      box-shadow: 3px 3px 35px 0 rgba($black, .24); } } }

.list {
  list-style: none;
  margin: 0;
  padding: 0;

  &__item {
    display: inline-flex;
    letter-spacing: px-to-rem(.5px);
    padding-left: 35px;
    position: relative;
    width: 100%;

    &:not(:last-child) {
      margin-bottom: px-to-rem(20px); }

    &-icon {
      flex-shrink: 0;
      left: 0;
      position: absolute;
      width: 25px;

      svg {
        backface-visibility: hidden; // webkit rendering bug-fix
        margin: 0 auto; } } } }

.tabs-nav {
  display: flex;
  margin: 0 auto;
  max-width: px-to-rem(1035px);
  @media(max-width: 767px) {
    flex-direction: column; }


  &__link {
    margin: auto px-to-rem(53px);
    width: 100%;
    @media(max-width: 1023px) {
      margin: auto 20px; }
    @media(max-width: 767px) {
      margin: 5px 0; }


    &::before,
    &::after {
      border-top: 1px solid;
      content: '';
      margin-top: -.5px;
      position: absolute;
      top: 50%;
      width: px-to-rem(54px);
      @media(max-width: 1023px) {
        width: 21px; }
      @media(max-width: 767px) {
        display: none; } }


    &::before {
      right: 100%; }

    &::after {
      left: 100%; }

    &:first-child {
      margin-left: 0;

      &::before {
        display: none; } }

    &:last-child {
      margin-right: 0;

      &::after {
        display: none; } } } }


.how-we-help {
  width: 100%;
  text-align: center;
  padding: 120px 0;
  position: relative;
  @media(max-width: 1023px) {
    padding: 2rem; }

  .patternsed {
    @media(max-width: 1023px) {
      display: none; }

    .js-prevent-inline {
      position: absolute;
      z-index: -1;
      width: 100px; }

    .triangle_6 {
      right: -15%;
      height: 400px;
      top: 100px;
      width: 900px; }

    .triangle_1 {
      right: 27%;
      top: 80px;
      width: 46px; }

    .triangle_2 {
      right: 29%;
      width: 36px;
      top: 40px;
      transform: scaleX(-1); }

    .triangle_3 {
      bottom: 100px;
      width: 80px;
      left: 20%;
 }      // transform: scaleY(-1)

    .triangle_4 {
      bottom: 20px;
      width: 101px;
      right: 10%;
      transform: rotate(-78deg); }

    .triangle_5 {
      left: 10%;
      width: 63px;
      top: 38%;
      transform: rotate(125deg); }

    .triangle_7 {
      right: 12%;
      width: 30px;
      bottom: 110px;
      transform: rotate(125deg); } }

  .how-we-help__title {
    font-size: 41px;
    text-align: center;
    font-weight: 400;
    padding-bottom: 39px;
    margin-bottom: 0;

    .h-text-gradient {
      font-weight: 500; } }

  .how-we-help__video_wr {
    width: 100%;
    max-width: 840px;
    height: 366px;
    background-color: #343546;
    margin: 0 auto;
    border-radius: 10px;
    @media(max-width: 767px) {
      height: 180px; } }

  .how-we-help__link {
    font-size: 21px;
    text-align: center;
    display: inline-block;
    padding-left: 20px;
    text-decoration: none;
    margin-top: 48px;
    color: rgba(255, 255, 255, .2);
    position: relative;
    padding-bottom: 6.5px;
    transition: .5s ease;
    @media(max-width: 767px) {
      padding-left: 0;
      width: 80%; }

    .link-youtube {
      width: 22px;
      height: 22px;
      display: inline;
      vertical-align: middle;
      opacity: .2;
      transition: .5s ease; }

    i {
      border: solid rgba(255, 255, 255, .2);
      border-width: 0 2px 2px 0;
      display: inline-block;
      padding: 3px;
      transform: rotate(-45deg) translateY(-120%);
      position: absolute;
      left: 0;
      top: 50%;
      transition: .5s ease;
      @media(max-width: 767px) {
        top: 27%; } }

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 1px;
      background-color: rgba(112, 112, 112, .1); }

    &:hover {
      color: rgba(255, 255, 255, .1);

      i {
        border-color: rgba(255, 255, 255, .1); }

      .link-youtube {
        opacity: .1; } } } }



.video_wrapper {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  border-radius: 10px; }


.videoIframe {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent; }


.videoPoster {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-image: url(/static/img/pictures/video/preload.png);
  background-size: cover;
  background-color: #000000;
  border: none;
  text-indent: -999em;
  overflow: hidden;
  opacity: 1;
  -webkit-transition: opacity 800ms, height 0s;
  -moz-transition: opacity 800ms, height 0s;
  transition: opacity 800ms, height 0s;
  -webkit-transition-delay: 0s, 0s;
  -moz-transition-delay: 0s, 0s;
  transition-delay: 0s, 0s;
  border-radius: 10px;
  opacity: .49; }

.video_wrapper:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 111px;
  height: 111px;
  transform: translateY(-50%) translateX(-50%);
  background-image: url(/static/img/pictures/video/play_button.svg);
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 50%;
  transition: .5s ease;
  z-index: 1;
  cursor: pointer; }

.video_wrapper.video-on:before {
  display: none; }


.video_wrapper:hover:before {
  // -webkit-box-shadow: 0px 0px 15px 4px rgba(93,165,253,1);
  // -moz-box-shadow: 0px 0px 15px 4px rgba(93,165,253,1);
  // box-shadow: 0px 0px 15px 4px rgba(93,165,253,1),
  // inset 0px 0px 15px 4px rgba(93,165,253,1),
  // inset 0px 0px 20px 20px rgba(93,165,253,.5)
  filter: drop-shadow(0 0 15px rgba(48,224,222,.75)); }


.videoPoster:hover {
  cursor: pointer; }


.videoWrapperActive .videoPoster {
  opacity: 0;
  height: 0;
  -webkit-transition-delay: 0s, 800ms;
  -moz-transition-delay: 0s, 800ms;
  transition-delay: 0s, 800ms; }

.close_video {
  display: none;
  position: absolute;
  top: 50px;
  right: 50px;
  width: 50px;
  height: 50px;
  // z-index: 10000

  span {
    position: absolute;
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
    width: 100%;
    height: 3px;
    background-color: rgba(255, 255, 255, .2);
    left: 0;

    &:nth-child(2) {
      transform: translateY(-50%) rotate(-45deg); } } }


.video_wrapper.video-on {
  @media(min-width: 1024px) {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    background-color: black;
    z-index: 100; } }

.video-on .close_video {
  display: block; }





