.trust_form {
  color: #fff;

  max-width: 895px;
  margin: 0 auto;
  margin: 0 auto 0;
  // @media(max-width: 768px)
  //   margin: 46px auto

  &__desc {
    margin: 0 auto 34px;
    @media(max-width: 1024px) {
      max-width: 585px; }
    @media(max-width: 768px) {
      padding-left: 20px;
      padding-right: 20px;
      margin-bottom: 18px; } }

  &__header {
    font-size: 48px;
    color: #fff;
    @media(max-width: 1024px) {
      font-size: 32px; }
    @media(max-width: 768px) {
      font-size: 24px; }

    text-align: center;
    margin: 46px auto 76px;
    @media(max-width: 1024px) {
      margin: 40px auto 28px; }
    @media(max-width: 768px) {
      margin: 38px auto 28px; } }

  &__star {
    color: #ff3131;
    font-weight: bold;
    font-size: 24px;

    &-bottom {
      transform: translateY(8px);

      display: inline-block;

      padding-right: 3px; } }

  &__label,
  &__input {
    display: block; }

  &__inp_grp {
    max-width: 585px;
    padding-bottom: 27px;
    @media(max-width: 1024px) {
      margin: 0 auto; }
    @media(max-width: 768px) {
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 18px; } }


  &__label {
    font-size: 24px;
    transition: all ease .4s;
    @media(max-width: 768px) {
      font-size: 16px; }

    padding-bottom: 4px;

    &.danger {
      color: red; }

    &-no_wrap {
      @media(min-width: 1025px) {
        white-space: nowrap; } } }

  &__marg {
    margin-bottom: 25px; }


  &__input {
    background: none;
    border: 0;
    outline: 0;

    border-bottom: 4px solid #474966;

    width: 100%;
    padding: 10px 0;

    font-size: 18px;
    @media(max-width: 768px) {
      font-size: 14px; }

    font-weight: 500;
    opacity: 0.9;
    color: #fff; }

  &__input-1 {
    padding: 0 0 10px;
 }    // margin-bottom: -5px

  &__container {
    display: block;
    position: relative;
    padding-left: 35px;
    // max-height: 18.4px
    @media(max-width: 768px) {
      padding-left: 25px; }

    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    @media(max-width: 768px) {
      font-size: 16px; }

    -webkit-user-select: none;
    -webkit-tap-highlight-color: transparent;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    .hid_input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;

      &:checked ~ .trust_form__checkmark:after {
        opacity: 1; } } }

  &__checkmark-1 {
    margin-top: 5px; }

  &__checkmark {
    position: absolute;
    top: 0;
    left: 0;

    // height: 28px
    // width: 28px
    height: 24.8px;
    width: 24.8px;
    @media(max-width: 1024px) {
      height: 22.8px;
      width: 22.8px; }
    @media(max-width: 768px) {
      height: 18.4px;
      width: 18.4px; }

    background-color: transparent;
    border: 1px solid  #5d5e79;
    border-radius: 50%;
    transition: all ease .3s;

    &:hover .hid_input ~ .trust_form__checkmark {
      border-color: #ccc; }

    &:after {
      content: "";
      position: absolute;
      opacity: 0;
      transition: all ease .3s; } }

  &__container .trust_form__checkmark:after {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 12px;
    height: 12px;

    background-color: #26e1a1;
    border-radius: 50%; } }

.trust_form__button {

  margin: 0;
  margin-top: 58px;
  font-family: MavenPro;
  font-size: 18px;
  font-weight: 500;
  line-height: 0.88;
  color: #ffffff;
  width: 146px;
  height: 50px;
  background-color: #27283a;
  border: none;
  border-radius: 30px;
  position: relative;
  cursor: pointer;
  outline: 0;

  &::after {
    position: absolute;
    top: -5px;
    bottom: -5px;
    left: -5px;
    right: -5px;
    background: linear-gradient(#00fccf, #00dbdf);
    content: '';
    z-index: -1;
    border-radius: 30px;
    transition: all ease .4s; }

  &:hover {
    &::after {
      opacity: .7; } }

  @media(max-width: 768px) {
    font-size: 16px;
    width: 105px;
    height: 42px;
    margin-top: 26px; } }

.trust .page-content-pattern::after {
  @media(max-width: 768px) {
    display: none; } }
