.layers-wrapper {
  padding: px-to-rem(113px) px-to-rem(30px);
  @media(max-width: 767px) {
    padding-bottom: 0; } }

@keyframes slideFromRight {
  0% {
    opacity: 0;
    pointer-events: none;
    transform: translateX(100vw); }

  100% {
    opacity: 1;
    pointer-events: none;
    transform: translateX(0); } }


.layers {
  margin: 0 auto;
  max-width: px-to-rem(1160px);
  @media(min-width: 768px) {
    .layers-item,
    .layers__image g {
      transform: translateX(100vw); }

    &.loaded {
      .layers-item,
      .layers__image g {
        animation: ease slideFromRight;
        position: relative;
        transform: translateX(0); }

      .layers-item:nth-child(1),
      .layers__image g:nth-of-type(3) {
        animation-duration: .9s; }

      .layers-item:nth-child(2),
      .layers__image g:nth-of-type(2) {
        animation-duration: 1.3s; }

      .layers-item:nth-child(3),
      .layers__image g:nth-of-type(1) {
        animation-duration: 1.7s; } }

    &.is-hovered {
      .layers-item,
      .layers__image g {
        &:not(.is-active) {
          opacity: .3; } }

      .layers__image g {
        &.is-active {
          transform: scale(1.1); } } } }


  &__content {
    display: flex;
    @media(max-width: 767px) {
      align-items: center;
      flex-direction: column; } }


  &__image {
    flex-shrink: 0;
    margin-right: 75px;
    width: px-to-rem(349px);
    @media(max-width: 1023px) {
      margin-right: 40px;
      margin-top: 20px; }
    @media(max-width: 767px) {
      margin: 40px auto 0;
      width: auto; }


    svg {
      overflow: initial; }

    g {
      transform-origin: center;
      transition: .4s ease; } }


  &__items {
    padding-top: px-to-rem(92px);
    @media(max-width: 767px) {
      padding-top: 25px; } } }


.layers-item {
  padding: 0 px-to-rem(25px);
  transition: .4s ease;
  @media(max-width: 767px) {
    padding: 15px 0 0;
    text-align: center; }


  &:nth-child(1) {
    .layers-item__title {
      color: #33bb95; } }

  &:nth-child(2) {
    .layers-item__title {
      color: #5bc8a9; } }

  &:nth-child(3) {
    .layers-item__title {
      color: #84d6bf; } }


  &__title {
    font-size: px-to-rem(30px);
    font-weight: normal;
    margin: 0;
    padding: 2px 0 8px;
    position: relative;
    text-transform: uppercase;
    @media(max-width: 767px) {
      font-size: 18px; }


    &::before {
      background: rgba(#8e91b7, .36);
      bottom: 4px;
      content: '';
      height: 1px;
      left: px-to-rem(-24px);
      position: absolute;
      right: px-to-rem(-24px);
      @media(max-width: 767px) {
        left: 0;
        right: 0; } }


    &::after {
      background: currentColor;
      border-radius: 50%;
      bottom: 0;
      content: '';
      height: 9px;
      left: px-to-rem(-32px);
      position: absolute;
      width: 9px;
      @media(max-width: 767px) {
        display: none; } } }



  &__description {
    color: $steel;
    font-weight: 300;
    letter-spacing: px-to-rem(.5px);
    line-height: 1.33; } }



