.top-wrapper {
  background: url('/static/img/pictures/top/bg.svg') top center no-repeat;
  background-size: auto 100%;
  padding: 0 px-to-rem(30px);
  position: relative;
  z-index: 9; }

.top {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: px-to-rem(980px);
  margin: 0 auto;
  max-width: px-to-rem(880px);
  padding: px-to-rem(230px) 0 0;
  text-align: center;
  @media(max-width: 1870px) {
    padding-top: px-to-rem(130px);
    max-width: initial; }

  @media(max-width: 1024px) {
    padding-top: px-to-rem(230px); }

  @media(max-width: 767px) {
    height: auto;
    padding-top: 100px; }


  &__title {
    filter: drop-shadow(5px 3px 2px #2c2c3c);
    background-image: linear-gradient(15deg, #00fccf 30%, #00a9f7 60%);

    @media(max-width: 1870px) {
      font-size: px-to-rem(40px);

      max-width: 720px; } }

  &__beta-btn {
    margin: px-to-rem(30px) 0 px-to-rem(40px); }

  &__description {
    font-size: px-to-rem(21px);
    font-weight: bold;
    letter-spacing: px-to-rem(.5px);
    @media(max-width: 767px) {
      font-size: 14px; } }


  &__description {
    font-weight: 300;

    b {
      font-weight: 500;
      letter-spacing: normal; } }

  &__socials {
    margin: px-to-rem(43px) 0 0 px-to-rem(-8px); }

  &__scroll-btn {
    margin: px-to-rem(70px) 0 0;
    @media(max-width: 767px) {
      margin: 35px 0 40px; } }

  // new

  &__title2 {
    font-size: px-to-rem(60px);
    font-weight: 500;
    color: #fff;

    margin-bottom: 0;
    padding-bottom: 23px;
    width: 100%;

    &.main__title {
      width: 120% !important; }

    @media(max-width: 1870px) {
      margin-top: px-to-rem(35px); }

    @media(max-width: 767px) {
      font-size: px-to-rem(23px); }

    &-col {
      color: #00ead7;

      position: relative;

      padding-left: 13px;

      &::before {
        content: '';
        position: absolute;

        left: -9px;
        top: 50%;
        transform: translateY(-50%);
        width: 13px;
        height: 14px;

        background: url('/static/img/icons/round.svg') top center no-repeat;
        background-size: contain;

        @media(max-width: 767px) {
          left: -1px;
          width: 8px;
          height: 10px; } }

      &-nopoint {
        &::before {
          content: none; } } } }

  &__btn {
    width: 544px;
    height: 60px;
    margin: 0 auto 45px;

    box-sizing: border-box;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;

    justify-content: center;
    align-items: center;

    // border: 5px solid  #00ead7
    border-radius: 30px;

    background-image: url('/static/img/icons/border.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;

    color: #fff;
    line-height: 50px;
    text-decoration: none;

    &:hover {
      .top__btn_img {
        transform: translateX(-5px); }

      .top__btn_span {
        transform: translateX(5px); } }

    @media(max-width: 767px) {
      width: 100%;

      border: 5px solid  #00ead7;

      background-image: none;
 } }      // margin: 0 10% 20px

  &__btn_img {
    width: 8px;
    height: 9px;
    margin-right: 10px;

    transition: all ease .4s; }

  &__btn_span {
    margin-right: px-to-rem(5px);

    transition: all ease .4s; }

  &-bold {
    font-weight: 500; }

  &-bold_700 {
    font-weight: 700;
    color: #fff; }

  &__hot {
    background-color: #fff;
    max-width: 76px; }

  &__widgets {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;

    @media(max-width: 1024px) {
      flex-wrap: wrap;

      justify-content: center; }

    &_img {
      width: 54px; } }

  &__widget {
    margin-right: 10px;

    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;

    justify-content: center;
    align-items: center;

    @media(max-width: 1024px) {
      margin-bottom: 10px; }

    @media(max-width: 767px) {
      width: 100%; }

    &-height {
      height: 63px; }
    &-height1 {
      height: 100px; } }

  &__widget1_img {
    width: 174px; }

  //timer

  &_timer {
    text-align: left;

    padding-bottom: 50px;

    &__desc {
      margin: 0;
      padding-bottom: 2px;
      margin-bottom: 10px;

      font-size: px-to-rem(17px);
      color: #fff;

      display: inline-block;

      border-bottom: 1px solid  #00FCCF; }

    &__wr {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;

      font-size: 41px;
      font-weight: bold;
      color: #dadada; }

    &-colored {
      color: #00fccf; } } }


.select-language {

  img {
    width: 20px; } }



