.competition {
  color: #e9e9e9;
  margin: 0 auto;
  max-width: px-to-rem(1000px);

  &__container {
    display: flex;
    @media(max-width: 767px) {
      flex-wrap: wrap; } }


  &__left {
    color: #fff;
    flex-shrink: 0;
    letter-spacing: normal;
    padding: px-to-rem(40px) 0 0 px-to-rem(50px);
    position: relative;
    width: px-to-rem(387px);
    @media(max-width: 767px) {
      padding: 0;
      text-align: center;
      width: 100%; }


    &::before,
    &::after {
      background: #fff;
      content: '';
      height: px-to-rem(5.6px);
      left: 0;
      position: absolute;
      top: 0;
      width: px-to-rem(53px);
      @media(max-width: 767px) {
        display: none; } }


    &::after {
      transform: rotate(90deg) translateY(px-to-rem(-5.6px));
      transform-origin: top left; } }

  &__title {
    font-size: px-to-rem(43px);
    font-weight: 500;
    line-height: 1.16;
    margin: 0;

    b {
      color: #fff;
      font-weight: bold; } }

  &__text {
    border-bottom: 1px solid rgba(#fff, .1);
    font-size: px-to-rem(22px);
    font-weight: 300;
    margin-bottom: px-to-rem(41px);
    margin-top: px-to-rem(13px);
    opacity: .49;
    padding-bottom: px-to-rem(37px); }

  &__link {
    .icon-link__icon {
      transform: rotate(-90deg); } }

  &__right {
    margin-left: auto;
    @media(max-width: 767px) {
      width: 100%; } }


  &__image {
    height: px-to-rem(723px);
    margin-bottom: px-to-rem(-30px);
    margin-top: px-to-rem(-100px);
    @media(max-width: 767px) {
      height: auto;
      margin: 20px auto 0;
      max-width: 300px; } }


  &__join_sm-form {
    padding-bottom: 40px;
    position: relative;

    &::after {
      border-right: 1px solid rgba(#fff, .05);
      bottom: 0;
      content: '';
      position: absolute;
      right: px-to-rem(-57px);
      top: px-to-rem(-41px); } }

  &__prizes {
    border-top: 1px solid rgba(#fff, .05);
