@keyframes moving {
  0% {}
  100% {
    transform: translateX(-50%) rotate(90deg); }
  50% {
    transform: translateY(-10px) translateX(-50%) rotate(90deg); } }

.comp_re__bg {
  top: 125px;
  position: absolute;
  @media(max-width: 1180px) {
    display: none; } }

.comp_wr {
  max-width: 1400px;
  width: 100%;
  padding-bottom: 50px;
  margin: 0 auto;
  overflow: hidden;
  text-align: center;
  font-weight: normal;

  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;

  justify-content: center;

  @media(max-width: 1180px) {
    padding-top: 100px; }

  @media(max-width: 1024px) {
    flex-wrap: wrap; }

  @media(max-width: 767px) {
    padding-top: 0; }

  &-colored {
    color: #29d9e3;
 }    // padding: 0 5px

  &-colored_yell {
    color: #ffbe15;
    margin: 0; }

  &-bold {
    font-weight: bold; }

  &__side {
    font-size: px-to-rem(25px);
    color: #fff;
    margin-top: 30px;
    width: 30%;
    width: calc(30% + 50px);
    position: relative;
    padding-left: 60px;
    text-align: left;
    min-height: 40px;
    line-height: 40px;
    width: 100%;
    @media(max-width: 1180px) {
      width: 100%;
      max-width: 400px;
      margin: 20px auto 0; } }

  &__icon {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%); }

  &__wrap {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;

    flex-wrap: wrap;

    height: 100px;
    margin-top: 200px;
    width: 29%;

    @media(max-width: 1024px) {
      width: 40%; }

    @media(max-width: 767px) {
      width: 100%;
      margin: 50px 0 0; } }

  &__center {
    float: left;
    width: 40%;
    margin: 0 -50px;
    // max-width: 500px
    height: 700px;
    background-image: url(/static/img/pictures/competition/comp_re.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position-x: 6px;
    position: relative;
    @media(max-width: 1180px) {
      margin: 0 auto;
      width: 90%;
      max-width: 500px;
      float: none; }

    @media(max-width: 1024px) {
      order: 3; }

    @media(max-width: 767px) {
      height: 540px;
      max-width: 450px; }

    &::after {
      content: '';
      position: absolute;
      width: 167px;
      height: 37px;
      left: 50%;
      bottom: 0;
      background-image: url(/static/img/pictures/competition/arrow.svg);
      transform: translateX(-50%) rotate(90deg);
      animation: moving 2s ease infinite; } }

  &__title {
    color: #fff;
    font-size: px-to-rem(164px);
    font-weight: bold;
    text-transform: uppercase;
    line-height: 1;
    padding-top: 120px;
    margin: 0;
    @media(max-width: 767px) {
      font-size: 80px; }
    &-short {
      width: 90%;
      @media(max-width: 767px) {
        width: 100%; } }
    &-marg {
      margin-bottom: 19%;
      @media(max-width: 1024px) {
        margin-bottom: 0; } } }

  &__btn {
    border-radius: 44px;
    background-color: #ffbe15;
    height: 87px;
    width: 358px;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 30px auto 14px;
    color: #000;
    text-decoration: none;
    font-size: 23px;
    transition: all ease .4s;
    position: relative;
    @media(max-width: 767px) {
      width: 100%;
      max-width: 350px;
      font-size: 20px; }

    &:hover {
      opacity: .8; }

    &::after,
    &::before {
      content: '';
      position: absolute;
      width: 167px;
      height: 37px;
      right: -250px;
      top: 50%;
      background-image: url(/static/img/pictures/competition/arrow.svg);
      transform: translateY(-50%); }

    &::before {
      right: auto;
      left: -250px;
      transform: translateY(-50%) rotate(180deg); }

    &-long {
      width: 400px;
      @media(max-width: 767px) {
        width: 100%;
        max-width: 350px; } }

    .comp_wr-bold {
      padding-left: 5px; } }

  &__desc {
    color: #fff;
    opacity: .7; }

  &__one {
    font-size: 7.9rem;
    font-weight: bold;

    @media(max-width: 767px) {
      font-size: 5rem; } }

  &__two {
    font-size: 6.2rem;

    margin: 0;

    @media(max-width: 767px) {
      font-size: 5rem; } }

  &__three {
    font-size: 7.5rem;
    font-weight: bold;

    @media(max-width: 767px) {
      font-size: 5rem; } }

  &__four {
    font-size: 8.6rem;
    font-weight: bold;

    @media(max-width: 767px) {
      font-size: 5rem; } } }





