    font-weight: normal;
    text-shadow: 0 7px 6px rgba(0, 0, 0, 0.16);
    background-color: #f72854;
    text-align: left;
    padding: 22px 0 22px 67px;
    // padding-left: 67px
    margin-left: -67px;
    position: relative;
    margin-top: 0;
    @media(max-width: 768px) {
