      padding: 20px;
      background-color: initial; }
    @media(max-width: 480px) {
      font-size: 18px;
      line-height: 26px; }

    &:before {
      position: absolute;
      content: '';
      left: -13px;
      top: 12px;
      width: 176px;
      height: 70px;
      background-color: #d5264b;
      z-index: -1;
      border-radius: 20px 0 0;
      @media(max-width: 768px) {
        display: none; } } }

  .social_check__title_number,
  .video_block__title_number {
    font-size: 68px;
    line-height: 80px;
    font-weight: normal;
    position: absolute;
    left: 6px;
    top: 50%;
    transform: translateY(-50%);
    @media(max-width: 768px) {
      display: none; } }

  .social_check__title_text {
    padding-left: 17px;
    @media(max-width: 480px) {
      text-align: center;
      padding-left: 0;
      display: block; } }

  .social_check__item {
    background-color: rgba(255, 255, 255, .01);
    position: relative;
    font-size: 18px;
    color: #ffffff;
    font-weight: normal;
    text-shadow: 0 7px 6px rgba(0, 0, 0, 0.16);
    display: block;
    // text-align: center
    height: 193px;
    margin-bottom: 22px;
    transition: all 1s ease;
    overflow: hidden;
    max-height: 1000px;
    @media(max-width: 340px) {
