      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: -16px; } }

  .social_check__item:last-child:after {
    content: '';
    bottom: -45px;
    height: 1px;
    width: 120px;
    background-color: rgba(112, 112, 112, .3); }

  .registr_page__socials {
    padding-left: 0;

    .item__block {
      // position: absolute;
      display: block;
      text-decoration: none;
      color: #ffffff;
