      display: block;
      display: flex;
      flex-wrap: nowrap;
      // height: 21px;
      display: inline-block;
      vertical-align: middle;

      &>* {
        margin: 0 4px;
        vertical-align: middle; }

      .cls-1 {
        fill: #24c48e;
        opacity: 1; } }


    .item__block_color {
      color: #24c48e;
      text-decoration: none; }

    .item__block_icon {
