      &:first-child {
        width: 19px; } } }

  .item__check {
    // position: absolute
    width: 30px;
    height: 32px;
    // top: 50%
    // transform: translateY(-50%)
    // left: 96px
    border-radius: 12px;
    background-color: rgba(255, 255, 255, .14);
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px;

    &:after {
