      background-image: url('/static/img/pictures/register/path.svg');
      background-size: contain;
      background-repeat: no-repeat;
      opacity: 0;
      bottom: 6px;
      left: 6px;
      transition: all ease .4s;
      cursor: pointer; } }

  // .registr_page__video_block,
  // .registr_page__video_block ~ .registr_page__arrow_down
  //   display: none

  .item__check.active:before {
    opacity: 1; }

  .ar_1,
  .ar_2,
  .ar_3 {
