  .ar_1 {
    .cls-1 {
      fill: #24c48e;
      opacity: 1; } }

  .social_check__desc {
    margin-top: 68px;
    text-align: center;
    font-size: 13px;
    color: rgba(255, 255, 255, .5);
    padding-bottom: 38px;
