      text-align: center;
      line-height: 34px;
      margin-bottom: 0; }
    @media(max-width: 480px) {
      font-size: 18px;
      line-height: 26px; }

    &:before {
      background-color: #1dae7d;
      top: 38px;
      left: auto;
      right: -10px;
      width: 281px;
      border-radius: 0 0 20px 0;
      height: 100%; } }



  .registr_page__video_wr {
    height: 472px;
    padding: 58px 67px;
