      top: 50%;
      background-color: rgba(112, 112, 112, .3);
      @media(max-width: 480px) {
        display: none; } }

    &:before {
      left: auto;
      right: 0; } }

  .check_email {
    text-align: center;
    padding-bottom: 10px; }
  .registr_page__warning {
    opacity: .5;
    text-align: center;
    font-size: 16px;
    display: block;
    margin-top: 12px; }

  .registr_page__warning_colored {
    color: #f62753;
    font-size: 26px; }

  // .bg
  //   img
  //     position: absolute
  //     width: 50px
  //     height: 50px
  .not_done {
    animation: blink 2s .5s ease;
    box-sizing: border-box;
    border: 1px rgba(255, 255, 255, 0) solid;

    @keyframes blink {
      0% {
        border: 1px #f72854 solid; }
      100% {} } } }

.registration .page-content-pattern::after {
  top: 100px;
  bottom: auto; }

.registr_page.socials_done .ar_2,
.registr_page.socials_done .ar_3 {
  .cls-1 {
    fill: #24c48e;
    opacity: 1; } }

